      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      position: absolute;
      width: 100%;
      right: 0;
      top: 62px;
      opacity: 0;
      visibility: hidden;
      z-index: 99;
      transition: all .5s; }
      header nav .gnb_list li {
