      header nav .gnb_list.is_open {
        opacity: 1;
        visibility: visible; }
    header nav .mobile-menu {
      position: absolute;
      top: 20px;
      right: 20px;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      width: 1.5em;
      height: 1.1em;
      transition: all .5s;
      cursor: pointer; }
      header nav .mobile-menu .bar {
        width: 100%;
        height: 2px;
        background: #fff;
        transition: all .5s; }
      header nav .mobile-menu:hover .bar {
        background: rgba(231, 76, 60, 0.6); }
      header nav .mobile-menu.is_open {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg); }
        header nav .mobile-menu.is_open .bar:first-child {
          -webkit-transform: rotate(90deg) translateX(35%);
                  transform: rotate(90deg) translateX(35%); }
        header nav .mobile-menu.is_open .bar:last-child {
          opacity: 0; }

@media screen and (min-width: 1028px) {
