@charset "UTF-8";
.color-main {
  color: #e74c3c; }

.bg-main {
  background: #e74c3c; }

.border-main {
  border: 1px solid #e74c3c; }

.fw-bold {
  font-weight: bold; }

.fw-thin {
  font-weight: thin; }

.ta-center {
  text-align: center; }

.ta-left {
  text-align-last: left; }

.ta-right {
  text-align-last: right; }

#home {
  background: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url("../../../assets/images/bg5.jpg") center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-items: center; }
  #home .home-wrap {
    width: 100%;
    padding: 0 80px; }
  #home .uploadBox {
    width: 42%;
    height: 500px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.7);
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    padding: 30px;
    float: left; }
    #home .uploadBox h2 {
      padding-bottom: 10px;
      border-bottom: 1px solid #aaa;
      margin-bottom: 30px; }
    #home .uploadBox .filebox {
      display: inline-block; }
      #home .uploadBox .filebox label {
        padding: .5em .75em;
        font-size: inherit;
        line-height: normal;
        vertical-align: middle;
        background-color: #eee;
        cursor: pointer;
        border-radius: .25em;
        color: #777; }
        #home .uploadBox .filebox label:hover {
          background-color: #d5d5d5; }
    #home .uploadBox .filebox input[type="file"] {
      /* 파일 필드 숨기기 */
      position: absolute;
      width: 1px;
      height: 1px;
      padding: 0;
      margin: -1px;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0; }
    #home .uploadBox .fileName {
      color: #fff; }
  #home .titleInfo {
    width: 58%;
    padding-top: 60px;
    padding-left: 60px;
    float: left; }
    #home .titleInfo h3 {
      color: #fff;
      font-size: 3rem;
      padding-bottom: 30px; }
      #home .titleInfo h3 span.title-concept {
        color: #fff;
        font-size: 3rem; }
    #home .titleInfo .startBtn {
      border: none;
      border-radius: 30px;
      padding: 10px 50px;
      font-size: .9rem;
      font-weight: bold;
      background: #fff;
      color: #333; }
      #home .titleInfo .startBtn span {
        color: #e74c3c; }

@media screen and (max-width: 1028px) {
  #home {
    display: block;
    padding-top: 100px; }
    #home .home-wrap {
      padding: 0 20px; }
    #home .uploadBox {
      width: 100%;
      height: 400px;
      padding: 30px;
      float: left; }
    #home .titleInfo {
      width: 100%;
      padding: 40px 20px;
      float: left; }
      #home .titleInfo h3 {
        font-size: 2.5rem;
        padding: 0 0 30px; }
        #home .titleInfo h3 span.title-concept {
          font-size: 3rem; } }

@media screen and (max-width: 768px) {
  #home .uploadBox {
    float: none; }
  #home .titleInfo {
    float: none; }
    #home .titleInfo h3 {
      text-align: center;
      font-size: 2rem;
      padding: 0 0 30px; }
      #home .titleInfo h3 span.title-concept {
        font-size: 2.5rem; }
    #home .titleInfo .startBtn {
      display: block;
      width: 261px;
      height: 42px;
      margin: 0 auto;
      background: #fff;
      color: #333; } }

@media screen and (max-width: 480px) {
  #home .uploadBox {
    padding: 20px;
    height: 200px; }
    #home .uploadBox h2 {
      margin-bottom: 20px; }
    #home .uploadBox .filebox label {
      font-size: .8rem; }
  #home .titleInfo {
    padding: 20px 5px; }
    #home .titleInfo h3 {
      font-size: 1.2rem;
      padding: 0 0 30px; }
      #home .titleInfo h3 span.title-concept {
        font-size: 1.5rem; } }
